






















































import Vue from "vue";

export default Vue.extend({
  data() {
    return {
      Size: 3,
      Color: "#000000",
      Tool: "brush",
      buttons: [
        {
          name: "brush",
          placeholder: "Brush",
          icon: require("../assets/brush.svg")
        },
        {
          name: "rect",
          placeholder: "Rect",
          icon: require("../assets/rect.svg")
        },
        {
          name: "circle",
          placeholder: "Circle",
          icon: require("../assets/circle.svg")
        },
        {
          name: "line",
          placeholder: "Line",
          icon: require("../assets/line.svg")
        },
        {
          name: "eraser",
          placeholder: "Eraser",
          icon: require("../assets/eraser.svg")
        }
      ],
      mainButtons: [
        {
          name: "share",
          placeholder: "Share",
          icon: require("../assets/share.svg"),
          event: "saveImgs"
        },
        {
          name: "undo",
          placeholder: "Undo",
          icon: require("../assets/undo.svg"),
          event: "undo"
        },
        {
          name: "download",
          placeholder: "Download",
          icon: require("../assets/download.svg"),
          event: "download"
        },
        {
          name: "clear",
          placeholder: "Clear",
          icon: require("../assets/delete.svg"),
          event: "clear"
        }
      ]
    };
  },
  methods: {
    select(value: string): void {
      this.Tool = value;
      this.$emit("selectTool", this.Tool);
    },
    setColor() {
      this.$emit("setColor", this.Color);
    }
  }
});
